.todo-item {
  display: flex;
  align-items: center;
  background: white;
  border-radius: 5px;
  box-shadow: 5px 5px 0 rgba(0,0,0,0.1);
  background: -webkit-gradient(linear, 0 0, 0 100%, from(#d9eaf3), color-stop(4%, #fff)) 0 4px;
  background-size: 100% 3rem;
  line-height: 3rem;
  margin: 0.5rem 0;
  padding: 10px 65px 10px 10px;
  min-width: 600px;
  max-width: 70%;
  min-height: 45px;
  height: auto;
  position: relative;
}

.todo-item:before {
  content: '';
  position: absolute;
  width: 4px;
  top: 0;
  right: 80px;
  bottom: 0;
  border: 1px solid;
  border-color: transparent #e0aa7e;
}

.todo-item__checkbox {
  display: none;
}

.checkbox-btn {
  width: 22px;
  height: 22px;
  border: 2px solid #747474;
  border-radius: 50%;
  margin-right: 1rem;
  background-color: #e9e9e9;
}

.checkbox-btn > svg {
  display: none;
  width: 100%;
  height: 100%;
}

input[type="checkbox"]:checked+label .checkbox-btn {
  border: none;
  width: 26px;
  height: 26px;
}

input[type="checkbox"]:checked+label .checkbox-btn>svg {
  display: block;
}

.todo-item__value {
  display: inline-flex;
  align-items: center;
  width: 95%;
  text-align: justify;
  margin: 0;
  color: #2b1000;
  font-size: 1.3rem;
}

.task--done {
  font-style: italic;
  text-decoration: line-through;
  font-weight: 400;
  color: #32ba7c;
}

.todo-item:last-child {
  margin-bottom: 0;
}

.todo-item__buttons-container {
  position: absolute;
  right: 5px;
  display: flex;
  justify-content: space-around;
  width: 70px;
}

.todo-item__input {
  display: block;
  box-sizing: border-box;
  -webkit-task-sizing: border-box;
  -moz-box-sizing: border-box;
  border: 2px solid #dfbe5a;
  width: 92%;
  padding: 10px;
  border-radius: 5px;
  font-size: 1.3rem;
}

.btn {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin: 2px 0 7px 0;
  background-color: transparent;
  border: none;
}

.btn--yellow:hover {
  background-color: #ffca2a;
}

.btn--pink:hover {
  background-color: #ee8f75;
}

.btn--green:hover {
  background-color: #7ad750;
}

.btn-aligner {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  width: 98%;
  color: #fff;
  font-size: 1.3em;
}

.todo-input__container {
  display: flex;
  flex-direction: row;
  width: 500px;
  justify-content: space-between;
}

.todo_input {
  width: 90%;
  height: 2rem;
  border: none;
  border-bottom: 2px solid #f9a400;
  padding-top: 0.2rem;
  padding-left: 0.6rem;
  box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.1);
  margin-bottom: 0.5rem;
  border-radius: 5px;
}

.todo_input--error {
  border: 2px solid #ed0000;
}

.new-task__btn {
  border: none;
  background-color: #ebcd65;
  border-radius: 50%;
  font-size: 1.8rem;
  font-weight: bold;
  width: 2rem;
  height: 2rem;
  padding: 0;
  margin-top: 0.18rem;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 100vw;
    min-height: 100vh;
    background-color: #dbe79f;
}

